@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
* {
    font-family: 'Roboto';
}

body {
    margin: 0;
    overflow: hidden;
}

#mainCanvas {
    position: absolute;
    z-index: -1;
}

#info {
    display: inline-block;
    position: absolute;
    background: #ffffff7f;
    width: 200px;
    border: 1px solid #3f3f3f;
    z-index: 0;
}

#info p {
    margin: 0;
}
#pauseButton {
    position: absolute;
    top: 0;
    margin-top: 3px;
    right: 0;
    margin-right: 3px;
    width: 32px;
    height: 32px;
    background: url(data:image/png;base64,);
    opacity: 0.22;
    border: none;
}

#pauseButton:hover {
    opacity: 0.9;
}

#pauseButton:active {
    opacity: 0.65;
}

#pauseMenu {
    position: absolute;
    width: 350px;
    height: 350px;
    left: 50%;
    margin-left: -175px;
    top: 50%;
    margin-top: -175px;

    text-align: center;
    
    background: rgba(23, 10, 97, 0.7);
    border: 1px solid rgba(37, 34, 71, 0.9);
    border-radius: 20px 0 0 20px;
    z-index: 0;
}

#pauseMenuContents {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
}

#pauseMenuContents button {
    background: rgb(91, 91, 194);
    border: 3px outset rgb(115, 115, 216);
    border-radius: 4px;
}

#pauseMenuContents p {
    color: white;
}
.sketch-picker {
    background: rgba(255, 255, 255, 0.4) !important;
}
#chat {
    position: absolute;
    width: 300px;
    bottom: 0;
    right: 12px;
    z-index: 0;
    background: rgba(162, 195, 255, 0.6);
    border-radius: 5px 5px 0 0;
}

#openChatButton {
    width: 100%;
    background: rgba(22, 174, 245, 0.1);
    border: 3px outset rgba(19, 70, 151, 0.43);
    border-radius: 5px 5px 0 0;
}

#openChatButtonText {
    margin-right: 3px;
}

#chatBox {
    display: none;
    height: 180px;
    border: 1px solid rgba(0, 0, 0, 0.43);
}

#chatMessages {
    height: 158px;
    overflow-y: scroll;
    word-break: break-word;
}

#sendChat {
    position: absolute;
    width: 99.5%;
    bottom: 0;
}

#sendChat input {
    position: absolute;
    width: 77.5%;
    left: 0;
    bottom: 0;
}

#sendChat button {
    position: absolute;
    width: 20%;
    right: 0;
    bottom: 0;
}

.player-icon {
    margin-bottom: -3px;
    margin-right: 5px;
}
#map {
    position: absolute;
    bottom: 4px;
    left: 4px;
    z-index: 500;
    background: rgba(0, 0, 0, 0.6);
    border: 1px solid rgba(255, 255, 255, 0.15);
    border-radius: 5px;
    cursor: all-scroll;
}
